.sign-up-div{
    background-color: #fff;
    padding: 5vh;
    align-items: center;
    border: 1px solid black;
    width: 80vh;
    min-height: 50vh;
    position: relative;
}
.sign-up-div h4{
    text-align: center;
    margin-top: 0;
}
.sign-up-div label{
    color: black;
    padding-left: 2vh;
}
.sign-up-div input[type=submit]{
    border: 1px solid black;
    padding: 10px 5px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    background-color: #222;
    color: #fff;

    align-self: center;
}
.text-input{
    border:1px solid grey;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 1vh;
}
.social-signin {
    background-color: #DD4B39;
    font-size: 3vh;
    color: white;
    padding: 2vh 4vh;
    font-weight: 500;
    letter-spacing: 1px;
    border-radius: 20px;
    border: none;    
    margin: 5vh;
    cursor: pointer;

}