.main-background{
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#817ee8+0,2b20ca+49,332ad8+50,2f1e9a+100 */
    background: rgb(18,51,6);
    background: linear-gradient(180deg, rgba(18,51,6,1) 0%, rgba(77,105,196,1) 0%, rgba(91,67,240,1) 0%, rgba(104,55,163,0.9640231092436975) 49%, rgba(118,40,168,1) 72%, rgba(106,4,171,1) 89%);
padding: 0px 3%;
height: 105vh;
width: 101%;

}
.header{
    display: flex;
    justify-content: space-between;
}
.flag-img-div{
    width: 70%;
    display: flex;
    align-content: center;
    text-align: center;
}
.flag-img-div h4{
   

}
.header-img{
    width: 2.5vh;
    height: 2.5vh;
    padding: 10px;
    margin: 10px;
    cursor: pointer;
}

.header-div-left{
    display: flex;
    justify-content: space-around;
    color: white;
}
.header-text{
    letter-spacing: 2px;
    color: white;
    margin-top:18px ;
    font-weight: 100;
    color: #d6d6d6;
}
.sub-header{
    display: flex;
    margin-top: 5vh;
    justify-content: space-between;
    
}
.sub-header h1{
    color: white;
    font-weight: 200;
    margin: 0;

}
.menu-img{
    width: 20px;
    height:20px;
    /* margin : 20px; */
    /* padding:20px; */
    margin-top: 10px;
}
.main-body{
    align-items: center;
    margin-top: 15vh;
    text-align: center;
}
.main-body h3{
    color: white;
    font-size: 1.5rem;
    font-weight: 200;
}
.sub-header-left img{
    margin-left:10px;
    margin-right:10px;
}
.main-body p{
    color: #d6d6d6;
    font-size: 1rem;
}
.body-input-div{
    display:flex;
    margin-top: 10vh;
    border-radius: 50%;
    align-items: center;
    align-self: center;
    margin-left: auto;
    display: inline-flex;
    margin-right: auto ;
}
.body-input{
    border-top-left-radius: 30px;
    border: none;
    border-bottom-left-radius: 30px;
    height: 50px;
    text-align: center;
    padding: 5px;
    font-weight: 300;
}
.body-input-btn{
    width: 50px;
    height:50px;
    background: white;
    border: none;
    padding:10px;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;

}
.footer-btn{
    width: 80px;
    position: absolute;
    bottom: 5vh;
    left:5vh;
    height: 80px;
    background-color: #f0bd16;
    border-radius: 50%;
    padding: 20px;
}
.sub-header-left{
    display: flex;

}
.sub-header-left img{
    width: 50px;
    height: 50px;
}
